"use client"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Linkedin, Facebook, Link as LinkIcon, Check, Mail, Share2 } from "lucide-react"

import { cn } from "@/lib/utils"

interface ShareJobProps {
  showLabel?: boolean
  label?: string
  className?: string
  buttonSize?: "default" | "sm"
}

export function ShareJob({ showLabel = true, label = "Share these opportunities:", className, buttonSize = "default" }: ShareJobProps) {
  const [copied, setCopied] = useState(false)
  const [hasNativeShare, setHasNativeShare] = useState(false)
  
  // As requested, sharing the main career page, not the specific job slug
  const url = "https://arboraistudio.com/career"
  const encodedUrl = encodeURIComponent(url)
  const encodedTitle = encodeURIComponent("We are hiring! Check out open roles at Arbor AI Studio.")

  useEffect(() => {
    // Check if the Web Share API is supported
    if (typeof navigator !== "undefined" && typeof navigator.share === "function") {
      setHasNativeShare(true)
    }
  }, [])

  const handleShare = async () => {
    // Prefer native share if available (mobile behavior)
    if (hasNativeShare) {
      try {
        await navigator.share({
          title: "Join Arbor AI Studio",
          text: "We are hiring! Check out open roles at Arbor AI Studio.",
          url: url,
        })
        return
      } catch (err) {
        console.error("Error sharing", err)
        // Fallback to copy if share fails or is cancelled
      }
    }

    // Fallback: Copy to clipboard
    try {
      await navigator.clipboard.writeText(url)
      setCopied(true)
      setTimeout(() => setCopied(false), 2000)
    } catch (err) {
      console.error("Failed to copy", err)
    }
  }

  const sizeClass = buttonSize === "sm" ? "w-7 h-7" : "w-8 h-8"
  const iconClass = buttonSize === "sm" ? "w-3 h-3" : "w-3.5 h-3.5"

  return (
    <div className={cn("flex flex-col sm:flex-row items-center gap-4", className)} role="region" aria-label="Share career opportunities">
      {showLabel && <span className="text-sm font-medium text-muted-foreground">{label}</span>}
      <div className="flex items-center gap-2">
        <Button
          variant="outline"
          size="icon"
          className={cn("rounded-full hover:text-[#0077b5] hover:border-[#0077b5]", sizeClass)}
          asChild
        >
          <a
            href={`https://www.linkedin.com/sharing/share-offsite/?url=${encodedUrl}`}
            target="_blank"
            rel="noopener noreferrer"
            aria-label="Share on LinkedIn"
          >
            <Linkedin className={iconClass} />
          </a>
        </Button>

        <Button
          variant="outline"
          size="icon"
          className={cn("rounded-full hover:text-[#1877F2] hover:border-[#1877F2]", sizeClass)}
          asChild
        >
          <a
            href={`https://www.facebook.com/sharer/sharer.php?u=${encodedUrl}`}
            target="_blank"
            rel="noopener noreferrer"
            aria-label="Share on Facebook"
          >
            <Facebook className={iconClass} />
          </a>
        </Button>

        <Button
          variant="outline"
          size="icon"
          className={cn("rounded-full hover:text-foreground", sizeClass)}
          asChild
        >
          <a
            href={`mailto:?subject=${encodedTitle}&body=Check out the open roles here: ${encodedUrl}`}
            aria-label="Share via Email"
          >
            <Mail className={iconClass} />
          </a>
        </Button>

        <Button
          variant="outline"
          size="icon"
          className={cn("rounded-full hover:text-primary hover:border-primary transition-all", sizeClass)}
          onClick={handleShare}
          aria-label={hasNativeShare ? "Share" : (copied ? "Link copied" : "Copy link")}
        >
          {copied ? (
            <Check className={cn("text-primary", iconClass)} />
          ) : hasNativeShare ? (
            <Share2 className={iconClass} />
          ) : (
            <LinkIcon className={iconClass} />
          )}
        </Button>
      </div>
    </div>
  )
}
